                <h3 class="section-title">devenir membre : les avantages</h3>
                <div class="row row-text">
                    <p>
                        En 1929, Alfred Mosher Butts, architecte new-yorkais alors au
                        <span class="mots-rose bold">chômage</span>
                        , invente le jeu. Une premièr version est présentée en 1931 sous le nom de Lexiko, mais
                        le succès est long à venir.  Les éditeurs restent frileux, malgré les
                        <span class="mots-rose italic">améliorations</span>
                        apportées - le plateau actuel date de 1938, les changements de nom  (Alph, puis
                        It, ou encore Crisscrosswords, puis enfin Scrabble en 1948). Le succès ne date que de 1952, sous l'impulsion de James Brunot. Selon certains, l'un des proprié
                        -
                        taires de Macy's, le plus grand magasin de New York, apprit à jouer pendant ses vacances d'été. De retour à New York, il entra dans une violente colère lorsqu'il
                        s'aperçut que le Scrabble était absent de ses rayons de jouets. L'Europe sera également longue à s'engouer. La version francophone a été créée en 1955, mais la
                        France a dû attendre 1965 et une opération publicitaire relayée par le Club Med' pour voir une nombre significatif d'adeptes s'affronter à coup de lettres chères.
                        Persperunt, vel in perrum reptae nis excepra quis in nulla asitas re mil ese dunt ut fuga. Faccatur?
                        Lit laut ratius quiate odi nam, quae omnis solum et fugitia ius, et hillend usciis andi omnihil lationsentis adion nonsequ aercium nis mo mo volorero del maximpor
                        sum ut aut quisciis inctur soluptio es dollor re, qui unto eaquis eossimus conecescid que optatio cum sectur sum dolor minctatur? Nonserum deliqua tiusam,
                        quias ni nonseque presto optatem ea imagnim quas ex expliqui cuptas ilique volent, nit odit, coribus reptae niantia pratiorro dollautaepel modis molor aciae
                        dolent re non repel ipsanie ndestin eturit eaquid etur maio blatum laturis samus none venestem res vel magnatet quidel id modit unt quidit fugit ipsae. As molum
                        di ipiciet venestr umquis doluptibus, vel il iduntet essitatur mos noneturi acerferum quostet as dolorro consedipit odipist, quatio torem cuptataquia delique dol
                        -
                        lab illiquia eum ame nate laciende velique volorectum conseressim facitatent.
                        Bis estiniati doluptur? Qui bla que ratiaessit es cusapiet facerias rescid que qui volorere dolorestis doluptatiae prenet volorup tationest, sundae pe prae conse-
                        quis elit modit optate aped que pel il incias volupta natem autae nimposapis eum aspis es dolorer uptateceptis nis et ero es sin non et ex exeriost quis dipisiti
                        debiti con exero oditatur mosanto etusdan duscid moluptur sum fuga. Nemporum is sam se liquatquid ullam lanisseque vellab ius esequid quat.
                    </p>
